/* Colours */
/* Typography */
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

td:not([align]),
th:not([align]) {
  text-align: left;
}

html, body {
  height: 100%;
}

body {
  background-color: #fff;
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: calc(16px + 0.3vw);
  line-height: 1.5;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

.gm-style img,
img[width],
img[height] {
  max-width: none;
}

p {
  margin-bottom: 1.4em;
  margin-top: 0;
}

p.lead {
  font-size: calc(1.3rem + 0.3vw);
}

h1, h2, h3, h4, h5, h6 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #333;
  text-rendering: optimizelegibility;
  margin-bottom: 0.7em;
  margin-top: 0;
  font-weight: 700;
  line-height: 1.2;
}

* + h1, * + h2, * + h3, * + h4, * + h5, * + h6, * + .h1, * + .h2, * + .h3, * + .h4, * + .h5, * + .h6 {
  margin-top: 1.4em;
}

h1, .h1 {
  font-size: calc(2.4rem + 0.3vw);
}

h2, .h2 {
  font-size: calc(2rem + 0.3vw);
}

h3, .h3 {
  font-size: calc(1.8rem + 0.3vw);
}

h4, .h4 {
  font-size: calc(1.6rem + 0.3vw);
}

h5, .h5 {
  font-size: calc(1.4rem + 0.3vw);
}

h6, .h6 {
  font-size: calc(1.2rem + 0.3vw);
}

a {
  color: #0087D0;
  text-decoration: none;
}

a:focus,
a:hover {
  color: #003451;
  text-decoration: underline;
}

blockquote {
  margin-left: 0;
  padding-left: 1em;
  border-left: 8px solid #efefef;
}

blockquote p {
  margin-bottom: 0.2em;
}

blockquote footer {
  color: #181822;
  font-size: 0.9rem;
}

ol, ul {
  padding-left: 1.5em;
}

dt {
  margin-top: 1em;
  font-weight: bold;
}

dd {
  margin-left: 0;
}

figure {
  margin: 0;
}

figcaption {
  margin-top: 0;
}

figcaption p {
  font-size: calc(0.9rem + 0.3vw);
}

input[type=email],
input[type=number],
input[type=search],
input[type=text],
input[type=tel],
input[type=url],
input[type=password],
textarea,
select {
  height: 38px;
  padding: 6px 10px;
  background-color: #fff;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
}

input[type=email],
input[type=number],
input[type=search],
input[type=text],
input[type=tel],
input[type=url],
input[type=password],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
}

textarea {
  min-height: 65px;
  padding-top: 6px;
  padding-bottom: 6px;
}

input[type=email]:focus,
input[type=number]:focus,
input[type=search]:focus,
input[type=text]:focus,
input[type=tel]:focus,
input[type=url]:focus,
input[type=password]:focus,
textarea:focus,
select:focus {
  border: 1px solid #6a6a6a;
  outline: 0;
}

label {
  display: block;
  margin: 0.5em 0;
  font-weight: 600;
}

input[type=checkbox],
input[type=radio] {
  display: inline;
}

input[type=submit],
input[type=reset],
input[type=button] {
  display: block;
  padding: 1em;
  margin: 0.5em 0;
  background: #59BACC;
  color: #fff;
  border-radius: 4px;
  transition: all 0.2s linear;
  border: 0;
}

input[type=submit].center,
input[type=reset].center,
input[type=button].center {
  margin: 0 auto;
}

input[type=submit]:hover,
input[type=reset]:hover,
input[type=button]:hover,
input[type=submit]:focus,
input[type=reset]:focus,
input[type=button]:focus {
  background: #ddd;
  text-decoration: none;
  color: #fff;
}

.required {
  color: #BF5D58;
}

table {
  margin-bottom: 1em;
}

table th,
table td {
  padding: 0.5em;
  border-bottom: 1px solid #efefef;
  text-align: left;
  -webkit-font-smoothing: antialiased;
}

table th {
  border-width: 2px;
}

table tr:last-child td {
  border-bottom: none;
}

table tr:nth-child(even) {
  background: #efefef;
}

table tbody tr:hover {
  background: #ddd;
}

.global-alert {
  background: #59BACC;
  text-align: center;
  padding: 1rem;
}

.global-alert p {
  color: white;
  margin: 0;
}

.btn {
  display: inline-block;
  border-radius: 4px;
  padding: 0 3rem;
  border: 1px solid #ddd;
  color: #6a6a6a;
  height: 40px;
  line-height: 40px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 700;
  font-size: 1rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
.btn:hover, .btn:focus {
  border-color: #c4c4c4;
  outline: 0;
  color: #6a6a6a;
  text-decoration: none;
  position: relative;
  top: -1px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
}

.btn--primary {
  background-color: #59BACC;
  border: 1px solid #59BACC;
  color: #fff;
}
.btn--primary:hover, .btn--primary:focus {
  border-color: #59BACC;
  color: #fff;
}

.content a {
  text-decoration: none;
}
.content abbr {
  border-bottom: 1px dotted;
  cursor: help;
}
.content cite {
  font-style: italic;
}
.content hr {
  background: #efefef;
  border: none;
  display: block;
  height: 1px;
  margin-bottom: 1.5em;
  margin-top: 1.5em;
}
.content img {
  vertical-align: text-bottom;
}
.content small {
  font-size: 0.9rem;
}
.content strong {
  font-weight: 700;
}
.content sub, .content sup {
  font-size: 0.9rem;
}
.content sub {
  vertical-align: sub;
}
.content sup {
  vertical-align: super;
}
.content p:last-child, .content dl:last-child, .content ol:last-child, .content ul:last-child, .content blockquote:last-child, .content pre:last-child, .content table:last-child {
  margin-bottom: 0;
}
.content p:empty {
  display: none;
}

.icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  width: 20px;
  height: 20px;
}

.icon--small {
  width: 13px;
  height: 13px;
}

.icon--medium {
  width: 26px;
  height: 26px;
}

.icon--large {
  width: 32px;
  height: 32px;
}

.icon--xlarge {
  width: 40px;
  height: 40px;
}

.icon--scale {
  width: 0.75em;
  height: 0.75em;
  vertical-align: middle;
  position: relative;
  top: -2px;
}

.icon--top {
  vertical-align: top;
}

.icon--bottom {
  vertical-align: bottom;
}

.icon--offset {
  margin-right: 5px;
}

.global-header {
  display: flex;
  justify-content: space-between;
  background: #eee;
}

.global-header__logo {
  font-size: 2rem;
  margin: 0;
}

.global-footer {
  margin-top: auto;
  padding: 1rem 0;
  text-align: center;
}

.social-links {
  display: flex;
  justify-content: center;
}

.social-links__item {
  margin: 0 1rem;
}

.social-links__link {
  color: black;
}

.global-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 1em;
  max-width: 780px;
}

.block {
  margin: 0;
}

.has-blue-color {
  color: #59BACC;
}

.has-blue-background-color {
  background-color: #59BACC;
}

.has-green-color {
  color: #58AD69;
}

.has-green-background-color {
  background-color: #58AD69;
}

.has-orange-color {
  color: #FFBC49;
}

.has-orange-background-color {
  background-color: #FFBC49;
}

.has-red-color {
  color: #E2574C;
}

.has-red-background-color {
  background-color: #E2574C;
}

/* Post title width */
.editor-post-title__block.wp-block {
  max-width: 1024px;
  /* Post title styling */
}
/* Main column width */
.wp-block {
  max-width: 1024px;
  /* Wide column width */
}
.wp-block[data-align=wide], .wp-block[data-align=full] {
  max-width: none;
}

.wp-block-quote:not(.is-large):not(.is-style-large) {
  border-left: 0;
  padding-left: 0;
}

.wp-block-image {
  line-height: 0;
}

.wp-block-button.is-style-full,
.wp-block-button.is-style-full .block-editor-rich-text,
div[data-type="core/button"] div[data-block] {
  display: block;
}

.wp-block-separator:not(.is-style-wide):not(.is-style-dots) {
  max-width: 100%;
}

.u-bold {
  font-weight: bold !important;
}

.u-semibold {
  font-weight: 600 !important;
}

.u-regular {
  font-weight: normal !important;
}

.u-light {
  font-weight: 300 !important;
}

.u-italic {
  font-style: italic !important;
}

.u-caps {
  text-transform: uppercase !important;
}

.u-left-align {
  text-align: left !important;
}

.u-center {
  text-align: center !important;
}

.u-right-align {
  text-align: right !important;
}

img.alignright {
  float: right;
  margin: 0 0 1em 1em;
}

img.alignleft {
  float: left;
  margin: 0 1em 1em 0;
}

img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.alignright {
  float: right;
}

.alignleft {
  float: left;
}

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media print {
  * {
    background: #ffffff;
    color: #000000 !important;
  }

  html {
    font: 100%/1.5 Arial, serif;
  }

  /**
   * Hide
   */
  .header,
.footer {
    display: none;
  }

  /**
   * Make full-width
   */
  .unit {
    margin: 0 !important;
    padding: 0 !important;
    width: 100% !important;
  }
}